<template>
  <div class="main-body" :style="{'-webkit-overflow-scrolling': scrollMode}">
    <v-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore">
      <ul class="list">

          <li v-for="(item, index) in proCopyright">
            <div>{{item.FZD_ZPMC}}</div>
          </li>

      </ul>

    </v-loadmore>

  </div>
</template>

<script>
import {Indicator,Loadmore} from 'mint-ui';
import { goodsList } from '../api/api';

export default {
    components:{
      'v-loadmore':Loadmore,
    },
  data () {
    return {
      pageNo:1,
      pageSize:50,
      proCopyright:[],
      allLoaded: false, //falsetrue
      scrollMode:"auto", //touchauto
      totalpage:0,
      loading:false,
      bottomText: '',
    }
  },
  mounted(){
    this.loadPageList();  //
  },
  methods:{
    loadBottom:function() {
      // 
      this.more();// 
      this.$refs.loadmore.onBottomLoaded();// 
    },
    loadPageList:function (){
      // 
      let _this = this;
        Indicator.open();
        let param = {
         access_token:sessionStorage.getItem("access_token"),
         page:this.page
        };
        goodsList(param).then(function (data) {
            Indicator.close();
            if(data.code == '0'){
                _this.proCopyrigh = data.content.data;
                _this.totalpage = Math.ceil(_this.proCopyrigh/this.pageSize);
                if(this.totalpage == 1){
                  this.allLoaded = true;
                }
                this.$nextTick(function () {
                  // 
                  this.scrollMode = "touch";
                  this.isHaveMore();
                });

                // for(var i =0;i<_this.goodslist.length;i++){
                //     _this.goodslist[i].amount = 0;
                // }
            }else{
               Toast(data.message);
            }
        })
    },
    more:function (){
      // 
      if(this.totalpage == 1){
        this.pageNo = 1;
        this.allLoaded = true;
      }else{
        this.pageNo = parseInt(this.pageNo) + 1;
        this.allLoaded = false;
      }

      console.log(this.pageNo);
      this.axios.get('/copyright?key='+ encodeURIComponent('')+"&mask=001"+"&page="+this.pageNo+"&size="+this.pageSize).then(res=>{
        this.proCopyright = this.proCopyright.concat(res.data.result.PRODUCTCOPYRIGHT);
        console.log(this.proCopyright);
        this.isHaveMore();
      });
    },
    isHaveMore:function(){
      // 
      //this.allLoaded = false; //true
      if(this.pageNo == this.totalpage){
        this.allLoaded = true;
      }
    }
  },
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    li{
      padding:30px 0;
      background-color: #ccc;
      margin-bottom:20px;
    }
</style>


